/* Variables ............................................................... */
:root {
	/* Screen colors */
	--screen-bg-color: #7b865e;
	--screen-text-color: #131514;

	/* Fake TFT effect params */
	--pixel-gap: 0.5px;
	--pixel-width: 1px;

	/* Key colors */
	--key-bg-color: black;
	--key-highlight-color: #5a5a5a;
	--key-text-color: rgba(255, 255, 255, 0.7);

	/* Noise image from StackOverflow - https://stackoverflow.com/a/13996191 */
	--noise: url(data:image/png;base64,);
}

/* Styles .................................................................. */

/* Create React App's webpack config will hoist all CSS @imports */

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: 'Press Start 2P', monospace;
}

html,
body {
	font-size: 4.5vw;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	overflow: hidden;
	position: relative;
	width: 100%;
	height: 100%;
}
html {
	background: var(--noise),
		-webkit-linear-gradient(
			left,
			rgba(0, 0, 0, 0),
			rgba(0, 0, 0, 0.3),
			rgba(0, 0, 0, 0)
		);
	background: var(--noise),
		linear-gradient(
			90deg,
			rgba(0, 0, 0, 0),
			rgba(0, 0, 0, 0.3),
			rgba(0, 0, 0, 0)
		);
	background-color: #0058bd;
}

/* If the aspect ratio is not tall enough, everything should be centered */
@media (min-aspect-ratio: 860/962) {
	html,
	body {
		font-size: 4vh;
	}
	main {
		width: 21rem;
	}
}

/* Screen .................................................................. */

.screen {
	position: relative;
	-webkit-flex: 1 1;
	        flex: 1 1;
	padding: 1rem;
	margin-top: 1rem;
	font-size: 1.5rem;
	line-height: 2rem;
	overflow-wrap: break-word;
	word-break: break-word;
	-webkit-hyphens: none;
	    -ms-hyphens: none;
	        hyphens: none;
	overflow: hidden;
	color: var(--screen-text-color);
	/* Add some super ugly skeumorphism */
	box-shadow: inset 0rem -1rem 10rem -1rem white,
		0 0 0.3rem 0.3rem rgba(0, 0, 0, 0.2);
	background-color: var(--screen-bg-color);
}

.screen::after {
	content: '';
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	/* Add even more ugly skeumorphism */
	background: var(--noise),
		-webkit-repeating-linear-gradient(
			left,
			var(--screen-bg-color) 0 var(--pixel-gap),
			transparent calc(2 * var(--pixel-gap))
				calc(2 * var(--pixel-gap) + var(--pixel-width))
		),
		-webkit-repeating-linear-gradient(
			top,
			var(--screen-bg-color) 0 var(--pixel-gap),
			transparent calc(2 * var(--pixel-gap))
				calc(2 * var(--pixel-gap) + var(--pixel-width))
		),
		-webkit-linear-gradient(
			left,
			rgba(255, 0, 0, 0.04),
			rgba(0, 255, 0, 0.2),
			rgba(0, 0, 255, 0.08)
		);
	background: var(--noise),
		repeating-linear-gradient(
			to right,
			var(--screen-bg-color) 0 var(--pixel-gap),
			transparent calc(2 * var(--pixel-gap))
				calc(2 * var(--pixel-gap) + var(--pixel-width))
		),
		repeating-linear-gradient(
			to bottom,
			var(--screen-bg-color) 0 var(--pixel-gap),
			transparent calc(2 * var(--pixel-gap))
				calc(2 * var(--pixel-gap) + var(--pixel-width))
		),
		linear-gradient(
			90deg,
			rgba(255, 0, 0, 0.04),
			rgba(0, 255, 0, 0.2),
			rgba(0, 0, 255, 0.08)
		);
	box-shadow: inset -0.1rem -0.1rem 0.5rem 0.1rem rgba(0, 0, 0, 0.5);
}
.screen figure {
	/* Nothing made sense, until I made it ALL CAPITALS */
	text-transform: uppercase;
	overflow: hidden;
}
.screen figure::after {
	/* This blinking underscore makes the user feel like a hacker */
	content: '_';
	-webkit-animation: 2s blink step-end infinite;
	        animation: 2s blink step-end infinite;
}

@-webkit-keyframes "blink" {
	from,
	to {
		opacity: 0;
	}
	50% {
		opacity: 1;
	}
}

@keyframes "blink" {
	from,
	to {
		opacity: 0;
	}
	50% {
		opacity: 1;
	}
}

/* Key ..................................................................... */

.key,
.key::after {
	/* Make the buttons round */
	border-radius: 50% 50%;
	overflow: hidden;
	-webkit-transition: box-shadow 0.05s, background 0.05s, -webkit-transform 0.05s;
	transition: box-shadow 0.05s, background 0.05s, -webkit-transform 0.05s;
	transition: transform 0.05s, box-shadow 0.05s, background 0.05s;
	transition: transform 0.05s, box-shadow 0.05s, background 0.05s, -webkit-transform 0.05s;
}
.key {
	position: relative;
	margin: 0.5rem;
	height: 2.5rem;
	/* Add some super ugly skeumorphism */
	box-shadow: 0 0 0.3rem 0 rgba(0, 0, 0, 0.5);
	background: var(--noise),
		-webkit-radial-gradient(
			top,
			ellipse,
			var(--key-highlight-color),
			var(--key-bg-color)
		);
	background: var(--noise),
		radial-gradient(
			ellipse at top,
			var(--key-highlight-color),
			var(--key-bg-color)
		);
	background-color: var(--key-bg-color);
	color: var(--key-text-color);
	border-width: 0;
}
.key:focus {
	/* This outline ruins the retro immersion */
	outline: 0;
}
.key::after {
	content: '';
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	/* Continue adding that super ugly skeumorphism */
	box-shadow: inset -0.1rem -0.1rem 1rem 0.1rem rgba(255, 255, 255, 0.2);
}
.key:active,
.key:active::after {
	/* Fake depression of button */
	-webkit-transform: translate(0.1rem, 0.1rem);
	        transform: translate(0.1rem, 0.1rem);
}
.key:active {
	/* Fake depression of button */
	box-shadow: 0 0 0.3rem 0 rgba(0, 0, 0, 0.5);
}
.key:active::after {
	/* Fake depression of button */
	box-shadow: inset -0.5rem -0.5rem 1rem 0.1rem rgba(255, 255, 255, 0.2);
}

.key figure {
	display: -webkit-flex;
	display: flex;
	padding: 0 1.1rem;
	/* It didn't look ugly enough without italic! */
	font-style: italic;
}

.key p,
.key figcaption {
	line-height: 2.433rem;
	vertical-align: middle;
}

.key p {
	-webkit-flex: auto;
	        flex: auto;
	font-size: 1.4rem;
	text-align: left;
}
.key p:last-child {
	text-align: center;
}
.key p + figcaption {
	-webkit-flex: 45% 1;
	        flex: 45% 1;
	display: block;
	text-transform: uppercase;
	text-align: left;
	font-size: 0.5rem;
	padding-left: 0.3rem;
}

/* Keyboard ................................................................ */

.keyboard {
	position: relative;
	display: grid;
	/* This will definitely not work in IE11 */
	grid: auto-flow / repeat(3, 1fr);
	margin: 1rem 0;
}

/* App ..................................................................... */

main {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	margin: 0 auto;
	padding: 1rem;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: stretch;
	        align-items: stretch;
	-webkit-align-content: space-between;
	        align-content: space-between;
}

